import React from "react";
import { Intro, Edu } from "./intro";
import Pub from "./pub";
import Exp from "./exp";
import Awd from "./award";
import Pro from "./pro";
import Skl from "./skills";
import { ASCII } from "./tool";
import { Path, Code, Contact } from "./tool";
import "./styles/main.css";

const paths = [
    '~\\Home', '~\\Project', '~\\Certificate', '~\\Experience', 
    '~\\Skills', '~\\Project', '~\\Award'
];

function IntroContent({ index }) {
    return (
        <div id='app'> 
            <ASCII />
            <Path path={paths[index-1]}/>
            <Code command='About Me'/>
            <Intro />
            <Path path={paths[index-1]}/>
            <Code command='Education'/>
            <Edu />
            <Path path={paths[index-1]}/>
            <Code command='Contact Information'/>
            <Contact />
        </div>
    );
}

function ProContent({ index }) {
    return (
        <div id='app'> 
            <ASCII />
            <Path path={paths[index-1]}/>
            <Code command='Project'/>
            <Pro />
            <Path path={paths[index-1]}/>
            <Code command='Contact Information'/>
            <Contact />
        </div>
    );
}

function PubContent({ index }) {
    return (
        <div id='app'> 
            <ASCII />
            <Path path={paths[index-1]}/>
            <Code command='Certificate Owned List'/>
            <Pub />
            <Path path={paths[index-1]}/>
            <Code command='Contact Information'/>
            <Contact />
        </div>
    );
}

function ExpContent({ index }) {
    return (
        <div id='app'> 
            <ASCII />
            <Path path={paths[index-1]}/>
            <Code command='Work and Internship'/>
            <Exp />
            <Path path={paths[index-1]}/>
            <Code command='Contact Information'/>
            <Contact />
        </div>
    );
}

function SklContent({ index }) {
    return (
        <div id='app'> 
            <ASCII />
            <Path path={paths[index-1]}/>
            <Code command='Skills'/>
            <Skl /> 
            <Path path={paths[index-1]}/>
            <Code command='Contact Information'/>
            <Contact />
        </div>
    );
}

function AwdContent({ index }) {
    return (
        <div id='app'> 
            <ASCII />
            <Path path={paths[index-1]}/>
            <Code command='Honors'/>
            <Awd />
            <Path path={paths[index-1]}/>
            <Code command='Contact Information'/>
            <Contact />
        </div>
    );
}

function getComponent(index) {
    switch (parseInt(index, 10)) {
        case 1:
            return <IntroContent index={index}/>;
        case 2:
            return <ProContent index={index}/>;
        case 3:
            return <PubContent index={index}/>;
        case 4:
            return <ExpContent index={index}/>;
        case 5:
            return <SklContent index={index}/>;
        case 6:
            return <AwdContent index={index}/>;
        default:
            return <div>Invalid index</div>;
    }
}

function App({ index }) {
    return (
        <div>
            {getComponent(index)}
        </div>
    );
}

export default App;
